  /*-- Button --*/
  .btn {
    background-color: transparent;
    border-radius: 0;
    color: inherit;
    font-size: 12px;
    font-weight: 700;
    height: 43px;
    letter-spacing: 0.4px;
    line-height: 23px;
    overflow: hidden;
    padding: 9px 50px;
    position: relative;
    text-transform: uppercase;
    z-index: 1;
  }
  .btn::before {
    -webkit-animation: 0.3s linear 0s normal forwards 1 running btnAnimationOut;
    animation: 0.3s linear 0s normal forwards 1 running btnAnimationOut;
    background-color: inherit;
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    -webkit-transform: scaleY(0.02);
    -ms-transform: scaleY(0.02);
    transform: scaleY(0.02);
    width: 21px;
    z-index: -1;
  }
  /*-- Button Animation --*/
  @-webkit-keyframes btnAnimationOut {
    0% {
      -webkit-transform: scaleY(1);
      transform: scaleY(1);
      width: 100%;
    }
    50% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
      width: 100%;
    }
    100% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
    }
  }
  @keyframes btnAnimationOut {
    0% {
      -webkit-transform: scaleY(1);
      transform: scaleY(1);
      width: 100%;
    }
    50% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
      width: 100%;
    }
    100% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
    }
  }
  /*-- Button Hover --*/
  .btn:hover {
    -webkit-transition: all 0.3s ease 0.3s;
    -o-transition: all 0.3s ease 0.3s;
    transition: all 0.3s ease 0.3s;
  }
  .btn:hover::before {
    -webkit-animation: 0.5s linear 0s normal forwards 1 running btnAnimationIn;
    animation: 0.5s linear 0s normal forwards 1 running btnAnimationIn;
  }
  /*-- Button Hover Animation --*/
  @-webkit-keyframes btnAnimationIn {
    0% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
    }
    50% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
      width: 100%;
    }
    100% {
      -webkit-transform: scaleY(1);
      transform: scaleY(1);
      width: 100%;
    }
  }
  @keyframes btnAnimationIn {
    0% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
    }
    50% {
      -webkit-transform: scaleY(0.02);
      transform: scaleY(0.02);
      width: 100%;
    }
    100% {
      -webkit-transform: scaleY(1);
      transform: scaleY(1);
      width: 100%;
    }
  }
