  /*-- Overlay --*/
  .overlay {
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
  }
  .overlay::before {
    background-color: #000;
    bottom: 0;
    content: "";
    left: 0;
    opacity: 0.7;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
  }
  .overlay-dark::before {
    background-color: #000000;
    opacity: 0.9;
  }
  .overlay-gradient::before {
    background: #2d3e50;
    background: -webkit-gradient(
      left top,
      left bottom,
      color-stop(0%, #2d3e50),
      color-stop(100%, #ed562d)
    );
    background: -webkit-gradient(
      left top,
      left bottom,
      color-stop(0%, #2d3e50),
      color-stop(100%, #ed562d)
    );
    background: -webkit-linear-gradient(top, #2d3e50 0%, #ed562d 100%);
    background: -o-linear-gradient(top, #2d3e50 0%, #ed562d 100%);
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#2d3e50),
      to(#ed562d)
    );
    background: linear-gradient(to bottom, #2d3e50 0%, #ed562d 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2d3e50', endColorstr='#ed562d', GradientType=0 );
    opacity: 0.9;
  }

