    margin: 0 5px;
  }
  .pagination ul li a {
    border: 1px solid #acacac;
    border-radius: 50%;
    color: #6d6d6d;
    display: block;
    font-size: 12px;
    font-weight: 300;
    height: 30px;
    line-height: 30px;
    text-align: center;
    width: 30px;
  }
  .pagination ul li a i {
    display: block;
    font-size: 18px;
    line-height: 30px;
  }
  .pagination ul li a:hover,
  .pagination ul li.active a {
    border: 1px solid #0056ff;
    color: #0056ff;
  }

