    margin: 20px auto 0;
    max-width: 650px;
  }
  .hero-content a {
    margin-top: 40px;
    display: inline-block;
  }

  .demo-item {
    text-align: center;
  }
  .demo-item .image {
    display: block;
    position: relative;
    transition: all 0.3s ease 0s;
  }
  .demo-item .image::after {
    background-color: #0056ff;
    content: "";
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
  .demo-item:hover .image {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
    transform: translateY(-10px);
  }
  .demo-item:hover .image::after {
    opacity: 0.8;
  }
  .demo-item .image i {
    border: 2px solid #fff;
    border-radius: 50%;
    color: #fff;
    font-size: 20px;
    height: 60px;
    left: 50%;
    line-height: 56px;
    margin-left: -30px;
    opacity: 0;
    position: absolute;
    top: 50%;
    transition: all 0.3s ease 0s;
    width: 60px;
    z-index: 9;
  }
  .demo-item:hover .image i {
    opacity: 1;
    margin-top: -30px;
  }
  .demo-item .image i:hover {
    border: 2px solid #ffffff;
    background-color: #ffffff;
    color: #0056ff;
  }
  .demo-item .image img {
    width: 100%;
  }
  .demo-item .title {
    font-size: 18px;
    font-weight: 400;
    margin: 30px 0 0;
    text-transform: uppercase;
  }
  .demo-item .title a {
    color: #3e3e3e;
    display: block;
    padding: 5px;
  }
  .demo-item .title a:hover {
    color: #0056ff;
  }

  .single-feature .icon {
    color: #0056ff;
    margin-right: 20px;
  }
  .single-feature .icon i {
    display: block;
    font-size: 36px;
  }

  .single-feature .content h4 {
    display: block;
    font-size: 18px;
    line-height: 13px;
    padding-bottom: 2px;
    text-transform: capitalize;
  }
  .single-feature .content p {
    color: #666;
    margin: 0;
  }

  .footer-section {
    background-color: #0056ff;
  }
  .footer-section h1 {
    color: #fff;
    display: block;
    float: left;
    font-size: 30px;
    margin-bottom: 20px;
  }
  .footer-section .buy-btn:hover {
    color: #0056ff;
    border-color: #ffffff;
    background-color: #ffffff;
  }

  .also-like .demo-item .title {
    font-size: 16px;
    font-weight: 400;
    margin: 22px 0 0;
    text-transform: capitalize;
  }

  /*=============================================
  =            custom style            =
  =============================================*/

  .buy-btn:hover {
    background-color: #f0542d;
    border-color: #f0542d;
    color: #ffffff;
  }
  .header-section .buy-btn {
    border-color: #ffffff;
    background-color: #ffffff;
    color: #000000;
    font-weight: 600;
  }
  .header-section .buy-btn:hover {
    border-color: #f0542d;
    background-color: #f0542d;
    color: #ffffff;
  }
  .stick.header-section .buy-btn {
    border-color: #000000;
    background-color: #000000;
    color: #ffffff;
  }
  .stick.header-section .buy-btn:hover {
    border-color: #f0542d;
    background-color: #f0542d;
    color: #ffffff;
  }
  .demo-item .image img {
    border: 1px solid #f1f1f1;
  }
  .demo-item:hover .image {
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
  }
  .demo-item .image::after {
    display: none;
  }
  .demo-item .image i {
    display: none;
  }
  .stick .buy-btn {
    border: 2px solid #f0542d;
    color: #f0542d;
  }
  .single-feature .icon {
    color: #f0542d;
    width: 50px;
  }
  .single-feature .content h4 {
    line-height: 1;
  }
  .footer-section {
    background-color: #444;
  }
  .stick .buy-btn:hover {
    color: #fff;
  }
  .demo-item .title a:hover {
    color: #f0542d;
  }
  .demo-item .title {
    text-transform: capitalize;
    font-weight: 500;
    margin-top: 20px;
  }
  .demo-item .title span {
    font-size: 13px;
  }
  .footer-section .buy-btn:hover {
    background-color: #f0542d;
    border-color: #f0542d;
    color: #ffffff;
  }
  .hero-content h1 {
    text-transform: none;
  }
  .hero-content h1 strong {
    text-transform: capitalize;
  }

  .logo a,
  .stick .logo a {
    width: auto;
  }
  @media only screen and (max-width: 767px) {
    .footer-section .buy-btn {
      float: left !important;
    }
  }

  /*=====  End of landing page  ======*/

  /*=============================================
  =            responsive            =
  =============================================*/

