  @media only screen and (min-width: 992px) and (max-width: 1169px) {
    .single-feature .content p {
      font-size: 14px;
    }
  }

  /* Tablet :768px. */

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    .hero-content h1 {
      font-size: 30px;
    }
    .demo-content h1 {
      font-size: 30px;
    }
    .demo-item .image i {
      height: 50px;
      line-height: 46px;
      width: 50px;
    }
    .single-feature .content p {
      font-size: 14px;
    }
