    }
    .hero-content h1 strong {
      font-size: 40px;
      margin-bottom: 15px;
    }
    .hero-content a {
      margin-top: 20px;
    }
    .demo-content h1 {
      font-size: 30px;
    }
    .section-title h1 {
      font-size: 30px;
    }
    .footer-section .buy-btn {
      float: left;
    }
  }

  /* small mobile :320px. */

  @media only screen and (max-width: 479px) {
